/* ExportTables.css */

/* Container for the component */
.export-tables {
  padding: 20px;
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Flexbox container for checkboxes */
.checkbox-group {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

/* Individual checkbox item */
.checkbox-item {
  margin-right: 15px;
  margin-bottom: 10px;
}

/* Label styling for checkbox */
.checkbox-item label {
  display: flex;
  align-items: center;
}

/* Checkbox input styling */
.checkbox-item input {
  margin-right: 5px;
}

/* Container for buttons */
.buttons-container {
  display: flex;
  justify-content: center;
  margin-bottom: 5px;
  gap: 10px; /* Space between buttons */
  
}

/* Toggle All and Export button styling */
.toggle-all-button,
.export-button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 4px;
}

/* Button hover effect */
.toggle-all-button:hover,
.export-button:hover {
  background-color: #0056b3;
}

/* Container for the rendered tables */
.tables {
  margin-top: 10px;
}

/* Styling for table container */
.table-container {
  margin-bottom: 20px;
}

/* Sidebar scroll styles */
.sidebar-scroll {
  position: relative;
  overflow: hidden;
}

.sidebar-scroll .scroll-content {
  overflow-y: auto;
  height: 100%;
  padding-right: 10px; /* Add some padding to avoid scrollbar overlapping the content */
}

.sidebar-scroll .scroll-content::-webkit-scrollbar {
  width: 6px; /* Thin scrollbar width */
}

.sidebar-scroll .scroll-content::-webkit-scrollbar-track {
  background: transparent; /* Transparent background */
}

.sidebar-scroll .scroll-content::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2); /* Semi-transparent thumb */
  border-radius: 10px; /* Rounded corners for the thumb */
}

.sidebar-scroll .scroll-content::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.4); /* Slightly darker when hovered */
}

